@use "../../_base";

.Signup {
  form {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
  }

  input {
    padding: 0.8rem;
    border-radius: 7px;
    font-size: 1rem;
    margin: 0.5rem 0 1.5rem 0;
    background-color: rgb(235, 235, 235);
  }

  label {
    color: rgb(201, 201, 201);
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1rem;
  }

  button {
    width: 100%;
    padding: 0.8rem 0;
    border-radius: 7px;
    background-color: #252525;
    box-shadow: 0 0px 0px rgb(157, 57, 26);
    color: white;
    font-weight: normal;
    font-size: 1rem;
    transition: 0.1s ease-out;
    box-shadow: 0 3px 3px rgba(0, 0, 0, 0.5);

    &:hover {
      cursor: pointer;
      background-color: rgb(255, 93, 43);
    }
  }
}
